<template>
  <div class="auth component-wrapper">
    <div class="window-wrapper">
      <div class="logo">
       <img  class="logo-image" src="@/assets/newImages/logo.svg" alt="logo" />
      </div>
      <form @submit.prevent="submitForm" class="login-form" id="login-form">
        <input v-model="email" type="text" id="email" name="email" :placeholder="$localizeFilter('email')"
          @change="v$.email.$touch" :class="{
            'error': v$.email.$error,
          }" />
        <span v-for="error in v$.email.$errors" :key="error.$uid" class="label-error">
          {{ error.$message }}
        </span>
        <input v-model="password" type="password" id="password" name="password"
          :placeholder="$localizeFilter('password')" :class="{
            'error': v$.email.$error,
          }" />
        <span v-for="error in v$.password.$errors" :key="error.$uid" class="label-error">
          {{ error.$message }}
        </span>
        <input v-model="lastName" type="text" id="lastName" name="lastName"
          :placeholder="$localizeFilter('last_name')" @change="v$.lastName.$touch" :class="{
            'error': v$.lastName.$error,
          }" />
        <span v-for="error in v$.lastName.$errors" :key="error.$uid" class="label-error">
          {{ error.$message }}
        </span>
        <input v-model="firstName" type="text" id="firstName" name="firstName"
          :placeholder="$localizeFilter('first_name')" @change="v$.firstName.$touch" :class="{
            'error': v$.firstName.$error,
          }" />
        <span v-for="error in v$.firstName.$errors" :key="error.$uid" class="label-error">
          {{ error.$message }}
        </span>
        <input v-model="sponsorEmail" type="text" id="sponsorEmail" name="sponsorEmail"
          :placeholder="$localizeFilter('sponsor_email')" @change="v$.sponsorEmail.$touch" :class="{
            'error': v$.sponsorEmail.$error,
          }" />
        <span v-for="error in v$.sponsorEmail.$errors" :key="error.$uid" class="label-error">
          {{ error.$message }}
        </span>
        <div class="terms-of-use">
          <input type="checkbox" id="terms" class="custom-checkbox" @change="v$.terms.$touch()" v-model="terms">
          <label for="terms" :class="{
            'terms-error': v$.terms.$error,
          }"><a href="#">{{ $localizeFilter("accept_terms_of_use") }}</a></label>
        </div>
        <button id="submitButton" type="submit" class="button" @click="submitHandler">
          {{ $localizeFilter("register") }}
        </button>
      </form>
    </div>
    <div class="links">
      <div class="forgot-password">
        <span>{{ $localizeFilter("already_have_an_account") + " " }}</span>
        <router-link :to="'/login'">{{ $localizeFilter("login").toUpperCase() }}</router-link>
      </div>
      <div class="main-page">
        <a @click.prevent="openHomePage">{{
          $localizeFilter("back_to_main")
        }}</a>
      </div>
    </div>
</div>
</template>

  
<script>
import useVuelidate from "@vuelidate/core";
import { required, email, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from '@/filters/localize.filter'



export default defineComponent({
  props: {
    token: {
      type: String,
      default: null
    },
    buyCoinTicker: {
      type: String,
      default: null
    }
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      email: "",
      password: "",
      firstName: "",
      lastName: "",
      // middleName: "",
      sponsorEmail: "",
      buttonKey: 1,
      terms: false,
    };
  },
  validations() {
    return {
      email: {
       required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
       email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email),
      },
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      firstName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      lastName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      // middleName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      sponsorEmail: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email),
      },
      terms: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
    };
  },
  methods: {
    openHomePage() {
      // ...
    },
    openBot() {
      // window.open('')
    },
    async submitHandler() {
      this.v$.$touch();
      if (this.v$.$error) {
        setTimeout(()=>{
          this.buttonKey+=1
        }, 1000)
        return;
      }
      const el = document.getElementById('submitButton'); 
      el.classList.add("button--loading");
      const formData = {
        email: this.email,
        firstName: this.firstName,
        lastName: this.lastName,
        sponsorEmail:this.sponsorEmail,
        password: this.password, 
        buyCoinTicker: this.buyCoinTicker
      }
      try {
        await this.$store.dispatch('register', formData)
        await this.$store.dispatch('login', formData)
        const {scope, email} = await this.$store.dispatch('getProfile')
        setTimeout(()=>{
          el.classList.remove("button--loading")
          if (scope=='not_activated_client') {
            this.$router.push('/confirmEmail?email='+email)
          } else {
            this.$router.push('/cabinet')
          }
          this.$message(localizeFilter('registration_successfull'))
        },1000)
      } catch (e) {
        setTimeout(()=>{
            this.$error(e)
            el.classList.remove("button--loading")
            this.buttonKey+=1
        }, 1000)
      }
    },
  },
  computed: {

  },
  async mounted() {
    if(this.token){
      localStorage.referralToken = this.token;
    }
    const token = this.token || localStorage.referralToken;
    try {
      if(token){
        const data = await this.$store.dispatch('getEmailByReferralToken',{token})
        this.sponsorEmail = data.email
      } else {
        this.sponsorEmail = 'root@capitalwhale.io'
      }
        document.getElementById('email').focus()
      } catch (e) {
          this.$error(e)
      }
  }
});
</script>

